@use '../../base/mixins' as *;
@use '../../base/variables' as *;
@use '../../base/media-queries' as *;

.categories {
    display: flex;
    align-items: center;
    gap: 9px;
    flex-wrap: wrap;

    @include mobile {
        justify-content: center;
    }

    li {
        padding: 14px 25px;
        border-radius: 30px;
        background-color: $color-gray2;
        color: $color-black2;
        font-weight: 700;
        transition: background $t-normal ease-in, color $t-normal ease-in;
        cursor: pointer;

        &:hover,
        &._active {
            background-color: $color-black;
            color: $color-white;
        }
    }
}