@use '../base/mixins' as *;
@use '../base/variables' as *;
@use '../base/media-queries' as *;

.header {
    position: relative;
    margin-bottom: 40px;
    border-bottom: 1px solid $color-gray-line;

    &__wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 40px 0 50px;

        @include mobile {
            padding: 20px 0;
        }
    }

    &__burger {
        position: relative;
        display: none;
        z-index: 2;

        @include mobile {
            display: flex;
            width: 20px;
            height: 12px;

            &:after,
            &:before {
                position: absolute;
                content: '';
                display: block;
                width: 100%;
                height: 2px;
                background: $color-orange;
                transition: background $t-normal ease-in transform $t-normal ease-in;
            }

            &:after {
                top: 0;
            }

            &:before {
                bottom: 0;
            }
        }
    }

    &__logo {
        display: flex;
        align-items: center;
        gap: 17px;

        h3 {
            font-weight: 800;
            font-family: Proxima Nova;
            letter-spacing: 1%;
            text-transform: uppercase;

            @include mobile {
                display: none;
            }
        }

        span {
            font-size: 16px;
            color: $color-gray;

            @include mobile {
                display: none;
            }
        }
    }

    &__actions {
        span {
            display: block;
            width: 1px;
            height: 20px;
            margin: 0 13px;
            background: $color-white;
        }

        svg {
            margin-right: 10px;
        }
    }

    &__nav {
        @include mobile {
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 100vh;
            opacity: 0;
            visibility: hidden;
            transform: translate3d(0, -100px, 0);
            background-color: $color-orange;
            transition: opacity $t-normal ease-in, visibility $t-normal ease-in, transform $t-normal ease-in;
        }

        ul {
            display: flex;
            gap: 20px;

            @include mobile {
                flex-direction: column;
                text-align: center
            }

            a {
                @include link-anim-show;

                @include mobile {
                    font-size: 20px;
                    color: $color-white;
                }

                

                &.active {
                    @include mobile {
                        text-decoration: underline;
                    }

                    @include tablet-p {
                        color: $color-orange;
                    }
                }
            }
        }
    }

    &.menu-open {
        .header__nav {
            @include mobile {
                opacity: 1;
                visibility: visible;
                transform: translate3d(0,0,0);
            }
        }

        .header__burger {
            @include mobile {
                &:after,
                &:before {
                    background: $color-white;
                }

                &:before {
                    transform: rotate(45deg) translate(0px, -7px);
                }

                &:after {
                    transform: rotate(-45deg) translate(0px, 7px);
                }
            }
        }
    }
}