@use '../../base/mixins' as *;
@use '../../base/variables' as *;
@use '../../base/media-queries' as *;

.sort {
    position: relative;

    &__title {
        position: relative;
        font-size: 14px;
        font-weight: 700;

        &--active {
            font-weight: 400;
            color: $color-orange;
            border-bottom: 1px dotted;
            cursor: pointer;
        }
    }

    &__body {
        position: absolute;
        top: calc(100% + 5px);
        right: 0;
        z-index: 1;
        padding: 13px 0;
        visibility: hidden;
        opacity: 0;
        transform: translate3d(0, -10px, 0);
        border-radius: 10px;
        background-color: $color-white;
        box-shadow: 0px 15px 20px 0px #00000008;
        transition: visibility $t-normal ease-in, opacity $t-normal ease-in, transform $t-normal ease-in;

        &._active {
            visibility: visible;
            opacity: 1;
            transform: translate3d(0, 0, 0);
        }

        li {
            padding: 10px 14px;
            font-size: 14px;
            cursor: pointer;
            transition: background $t-normal ease-in, color $t-normal ease-in;

            &:hover,
            &._active {
                background-color: $color-orange-opacity;
                color: $color-orange;
            }
        }
    }
}