@use 'mixins' as *;
@use 'variables' as *;
@use 'media-queries' as *;

html, body {
	min-height: calc(var(--app-height, 1vh) * 100);
    margin: 0 !important;
}

body {
	overflow-x: hidden !important;
    overflow-y: scroll !important;
    font: 400 res(16, 17)/1.3 $font-main;
    color: $color-black;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: 100%;

    @include desktop-xxl {
        font-size: 17px;
        line-height: 24px;
    }

    &.disable-scroll {
        overflow-y: scroll !important;
    }

}

.container {
    max-width: 100%;
    width: $container-width;
    margin: 0 auto;
    padding: 0 15px;

    @include mobile {
        width: 100%;
    }
}

.text-bold {
    font-weight: 700;
}

.text-center {
    text-align: center;
}

@media (hover: hover) {
    ::-webkit-scrollbar {
        width: 4px;
    } 

    ::-webkit-scrollbar-track {
        background-color: transparent;
    }

    ::-webkit-scrollbar-thumb {
        background-color: $color-orange;
    }
}

h1, .h1 {
    margin-bottom: 35px;
    font: 700 res(32, 38)/res(38, 44) $font-main;
    text-transform: initial;

    @include desktop-xxl {
        font-size: 38px;
        line-height: 44px;
    }
}

h2, .h2 {
    margin-bottom: 35px;
    font: 700 res(28, 36)/res(32, 40) $font-main;
    text-transform: initial;

    @include desktop-xxl {
        font-size: 32px;
        line-height: 40px;
    }
}

h3, .h3 {
    font: 700 res(19, 24)/res(23, 28) $font-main;
    text-transform: initial;

    @include desktop-xxl {
        font-size: 24px;
        line-height: 28px;
    }
}

main {
    p {
        &:empty {
            &:after {
                content: '&nbsp;';
                opacity: 0;
                visibility: hidden;
            }
        }
        
        a {
            cursor: pointer;
            // @include anim-underline-show();
        }
    }
}


// Picture style
picture {
    position: relative;
    overflow: hidden;
    display: block;
    padding-bottom: 100%;

    img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 1;
        object-fit: cover;
        content-visibility: auto;
        transition: transform $t-medium;
    }
}

a:not(.swiper-slide) {
    @include hover {
        picture img, video {
            transform: scale3d(1.2, 1.2, 1.2);
        }
    }
}
