@use 'mixins' as *;
@use 'variables' as *;
@use 'media-queries' as *;

.action {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: 10px 20px;
    box-shadow: none;
    font-size: 16px;
    text-align: center;
    border-radius: 30px;
    transition: background-color $t-normal, color $t-normal, border-color $t-normal;
    cursor: pointer;
    overflow: hidden;

    &-primary {
        border: 1px solid;
        background-color: $color-orange;
        color: $color-white;

        &:hover {
            background-color: $color-white;
            color: $color-orange;
        }
    }

    &-secondary {
        border: 1px solid;
        background-color: transparent;
        color: $color-orange; 

        &:hover {
            background-color: $color-orange;
            color: $color-white;
        }
        
        svg {
            margin-right: 8px;
        }
    }
}

.link {
    cursor: pointer;

    &__anim-hide {
        @include link-anim-hide;
    }

    &__anim-show {
        @include link-anim-show;
    }
}