@use '../../base/mixins' as *;
@use '../../base/variables' as *;
@use '../../base/media-queries' as *;

.catalog {
    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 10px;
        margin-bottom: 32px;

        @include mobile {
            flex-direction: column;
            gap: 30px;
        }
    }

    &__body {
        display: grid;

        @include mobile {
            grid-template-columns: repeat(1, 1fr);
            gap: 20px 10px;
        }

        @include tablet-p {
            grid-template-columns: repeat(2, 1fr);
            gap: 30px 20px;
        }

        @include tablet-l {
            grid-template-columns: repeat(3, 1fr);
            gap: 30px 20px;
        }

        @include laptop {
            grid-template-columns: repeat(4, 1fr);
            gap: 65px 35px;
        }
    }
}