@use 'variables' as *;

// hover
@mixin hover {
    @media (hover: hover) and (pointer:fine) {
        &:hover {
            @content;
        }
    }
}

// resize mixin for value
// Example:
// margin: 0 auto res(31, 64) - margin: 0 auto calc(64px + (31 - 64) * ((100vw - 414px) / (1920 - 414)))
@function res($minValue, $maxValue, $symbol: plus, $minWidth: 430, $maxWidth: 1920) {
    @if $symbol == plus {
        @return calc(#{$minValue}px + (#{$maxValue} - #{$minValue}) * ((100vw - #{$minWidth}px) / (#{$maxWidth} - #{$minWidth})));
    } @else {
        @return calc(-1 * (#{$minValue}px + (#{$maxValue} - #{$minValue}) * ((100vw - #{$minWidth}px) / (#{$maxWidth} - #{$minWidth}))));
    }
}

// links
@mixin link-anim-show($line-width: 100%) {
    display: inline;
    background: linear-gradient(to top, currentColor, currentColor) 0% 98% / 0% .05em no-repeat;
    transition: background-size .3s cubic-bezier(0.5,0.25,0,1);

    @include hover {
        background-size: #{$line-width} .05em;
        background-position: 96% 98%;
    }
}

@mixin link-anim-hide($line-width: 100%) {
    display: inline;
    background: linear-gradient(to top, currentColor, currentColor) 96% 98% / #{$line-width} .05em no-repeat;
    transition: background-size .3s cubic-bezier(0.5,0.25,0,1);

    @include hover {
        background-size: 0% .05em;
        background-position: 0% 98%;
    }
}