:root {
    --icon__dropdown: url("data:image/svg+xml,%3Csvg width='8' height='14' viewBox='0 0 8 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.25 1.5L6.75 7L1.25 12.5' stroke='%230E0E0E' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
}

// fonts
$font-main             :  'Proxima Nova', Arial, Helvetica, sans-serif;

// colors
$color-black                 : #232323;
$color-black2                : #2C2C2C;
$color-background            : #ffdf8c;
$color-gray-line             : #f6f6f6;
$color-orange                : #fe5f1e;
$color-orange-opacity        : #FE5F1E0D;

$color-white                 : #ffffff;
$color-gray                  : #7B7B7B;
$color-gray2                 : #F9F9F9;
$color-gray3                 : #F3F3F3;

$container-width       : 90%;

// transition
$t-fast                : .15s;
$t-normal              : .3s;
$t-medium              : .5s;
$t-large               : .7s;
$cubic-bezier          : cubic-bezier(0.545, 0.045, 0.355, 1); 

// breakpoints
$screen-xxs-max        : 390px;
$screen-xs-max         : 430px;
$screen-sm-max         : 767px;
$screen-sm-min         : 768px;
$screen-md-max         : 1023px;
$screen-md-min         : 1024px;
$screen-lg-max         : 1279px;
$screen-lg-min         : 1280px;
$screen-xl-min         : 1440px;
$screen-xxl-min        : 1920px;

// elements
$arrow                 : var(--icon__dropdown);