@use '../base/mixins' as *;
@use '../base/variables' as *;
@use '../base/media-queries' as *;

.cart {
    &__wrap {
        display: flex;
        flex-direction: column;
        gap: 30px;

        @include mobile {
            gap: 15px;
        }
    }

    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-bottom: 30px;
        border-bottom: 1px solid $color-gray2;

        h2 {
            margin-bottom: 0;
        }

        button {
            color: $color-gray;
        }
    }

    h2 {
        display: flex;
        align-items: center;
        gap: 17px;
    }

    &__item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        gap: 15px;
        padding: 30px 0;

        &:not(:last-child) {
            border-bottom: 1px solid $color-gray2;
        }

        &--image {
            width: 80px;
        }

        &--info {
            max-width: 280px;
            width: 100%;

            p {
                color: $color-gray;
            }
        }

        &--quantity {
            display: flex;
            align-items: center;

            span {
                padding: 0 12px;
                font-size: 22px;
                line-height: 1;
                font-weight: 700;
            }

            button {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 32px;
                height: 32px;
                color: $color-orange;
                border-radius: 50%;
                border: 2px solid;
                transition: color $t-normal ease-in, background $t-normal ease-in;

                @include hover {
                    color: $color-white;
                    background: $color-orange;
                }
            }
        }

        &--summa {
            font-size: 22px;
            font-weight: 700;
        }
    }

    &__bottom {
        margin-top: 20px;
        padding-bottom: 40px;

        &--line {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 20px;
            font-size: 22px;

            @include mobile {
                flex-direction: column;
            }

            button {
                max-width: 220px;

                @include mobile {
                    margin-bottom: 15px;
                }
            }
        }
    }
}