@use '../base/mixins' as *;
@use '../base/variables' as *;
@use '../base/media-queries' as *;

.star-rating {
    display: flex;
    gap: 4px;

    &.readonly {
        .star {
            cursor: default;
            pointer-events: none;
        }
    }

    .star {
        font-size: 24px;
        color: $color-gray;
        cursor: pointer;
        transition: color 0.2s ease, transform 0.2s ease;

        &.selected,
        &.hovered {
            color: $color-orange;
        }

        &.clicked {
            animation: pulse 0.3s ease;
        }
    }
}

@keyframes pulse {
0% {
    transform: scale(1);
}
50% {
    transform: scale(1.4);
}
100% {
    transform: scale(1);
}
}