@use '../../base/mixins' as *;
@use '../../base/variables' as *;
@use '../../base/media-queries' as *;

.product__card {
    &--name {
        margin: 11px 0 22px;
    }

    &--info {
        display: flex;
        flex-direction: column;
        gap: 7px;
        padding: 7px;
        border-radius: 10px;
        background: $color-gray3;

        > div {
            display: flex;
            gap: 1;

            span {
                display: inline-block;
                flex: 1;
                padding: 8px;
                border-radius: 5px;
                font-size: 14px;
                font-weight: 700;
                text-align: center;
                cursor: default;
                transition: background $t-normal ease-in;

                &:not(.disabled) {
                    cursor: pointer;

                    &:hover,
                    &._active {
                        background: $color-white;
                    }
                }

                &.disabled {
                    opacity: 0.4;
                }
            }
        }
    }

    &--rating {
        margin: 15px 0;
        .star {
            color: $color-gray;
            font-size: 20px;

            &.filled {
                color: $color-orange;
            }
        }
    }

    &--bottom {
        &-price {
            margin-bottom: 10px;
            font-size: 22px;
            font-weight: 700;
        }
    }

    &--addToCart {
        .count {
            margin-left: 10px;
        }
    }
}